import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { Container } from "@material-ui/core";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardHeader from "@material-ui/core/CardHeader";
import { makeStyles } from "@material-ui/core/styles";
import Avatar from "@material-ui/core/Avatar";
import Typography from "@material-ui/core/Typography";
import Footer from "./Footer/Footer";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import { Grid } from "@material-ui/core";
import { Control, LocalForm } from "react-redux-form";
import CloseIcon from "@material-ui/icons/Close";
import "./Forum/Forum.css";
import CustomLoader from "./Loader";

const useStyles = makeStyles((theme) => ({
  card: {
    width: "100%",
  },
  content: {
    width: "100%",
  },
  cardAns: {
    width: "98%",
    marginLeft: "2%",
    marginTop: "10px",
  },
  avatar: {
    backgroundColor: "#e03268 !important",
  },
}));

function ThreadDetailComponent({ answers }) {
  const classes = useStyles();
  if (answers.length > 0) {
    return answers.map((ans) => {
      return (
        <Card className={classes.cardAns}>
          <CardHeader
            style={{ backgroundColor: "#f6f3f1" }}
            avatar={
              <Avatar aria-label="recipe" className={classes.avatar}>
                {ans.author[0].toUpperCase()}
              </Avatar>
            }
            title={ans.author}
            subheader={ans.date}
          />
          <CardContent className={classes.content}>
            <Typography variant="body2" color="textPrimary" component="p">
              {ans.description}
            </Typography>
            {/* <img src={ans.assets[0].image} width={50} height={50} /> */}
          </CardContent>
        </Card>
      );
    });
  } else {
    return <div>No answers yet</div>;
  }
}

function Answer({ open, handleClose, postQuestion, setfiles, files }) {
  // const [file, setFile] = useState([]);

  const handleSubmit = (values, files) => {
    console.log(values, files);
    handleClose();
    postQuestion(values, files);
  };

  const getFile = (e) => {
    setfiles(e.target.files, false);
  };

  const Close = () => {
    setfiles([], true);
    handleClose();
  };

  return (
    <Dialog
      className="editModal"
      fullWidth
      open={open}
      onClose={Close}
      scroll="body"
      aria-labelledby="scroll-dialog-title"
      aria-describedby="scroll-dialog-description"
    >
      <div className="d-flex flex-row">
        <DialogTitle id="scroll-dialog-title">Answer</DialogTitle>
        <div className="ml-auto">
          <DialogActions>
            <Button style={{ backgroundColor: "white", outline: "none" }}>
              <CloseIcon
                onClick={Close}
                style={{ backgroundColor: "white", margin: 10 }}
              />
            </Button>
          </DialogActions>
        </div>
      </div>
      <DialogContent dividers={true}>
        <LocalForm onSubmit={(values) => handleSubmit(values, files)}>
          <Grid row style={{ marginBottom: 15 }}>
            <label htmlFor="Answer">Your Answer</label>
            <Control
              component="textarea"
              model=".Answer"
              id="Answer"
              name="Answer"
              placeholder="Answer"
              style={{ width: "100%" }}
            />
          </Grid>
          <Grid row style={{ marginBottom: 15 }}>
            <Button
              variant="contained"
              component="label"
              color="secondary"
              fullWidth
            >
              Attachments &nbsp;
              <input
                type="file"
                id="files"
                name="files"
                // multiple
                onChange={getFile}
                accept="image/*"
                style={{ outline: "none", display: "none" }}
              />
            </Button>
            {Object.values(files).map((file) => {
              return <div>{file.name}</div>;
            })}
          </Grid>
          <Grid row style={{ marginBottom: 15 }}>
            <Button
              variant="contained"
              type="submit"
              fullWidth={true}
              style={{
                backgroundColor: "#09bfad",
                color: "white",
                outline: "none",
              }}
            >
              Post
            </Button>
          </Grid>
        </LocalForm>
      </DialogContent>
    </Dialog>
  );
}

function ThreadDetail({ thread, followUp }) {
  const [open, setOpen] = useState(false);
  const [files, setFiles] = useState([]);

  const toggleModal = () => {
    setOpen(!open);
  };

  const postQuestion = (values, files) => {
    console.log(thread.id, values.description, files);
    followUp(thread.id, values.Answer, files);
  };

  const setfiles = (Files, close) => {
    console.log("calllleeeeeeeeddddddddddd");
    if (close) {
      setFiles([]);
    } else {
      console.log(Files);
      let prevFiles = files;
      let arr = [];
      let arr1 = [];
      let arr2 = [];
      for (let i = 0; i < Files.length; ++i) {
        for (let j = 0; j < prevFiles.length; ++j) {
          if (Files[i].name === prevFiles[j].name) {
            console.log("files[i].name", Files[i].name);
            console.log("prevFiles[j].name", prevFiles[j].name);
            arr.push(i);
          }
        }
      }
      console.log("arr", arr);
      arr1 = Object.values(Files);
      console.log("arr1 before", arr1);
      for (let i = 0; i < arr.length; ++i) {
        console.log(i);
        arr1.splice(arr[i], 1, null);
        console.log(arr1);
      }
      arr1 = arr1.filter((a) => a !== null);
      console.log("arr1", arr1);
      arr2 = Object.values(prevFiles);
      arr2.pop();
      console.log("arr2", arr2);
      arr1 = arr2.concat(arr1);
      let finalObj = { ...arr1 };
      finalObj.length = Object.keys(finalObj).length;
      console.log("final", finalObj);
      setFiles(finalObj);
      console.log(files);
    }
  };

  const classes = useStyles();
  return (
    <>
      <Container className="forum" maxWidth="lg" style={{ marginBottom: 40 }}>
        <div style={{ display: "flex", flex: "row" }}>
          <div>
            <h2>{thread.title}</h2>
            <small>{thread.date}</small>
            <small>
              &nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;{thread.followups.length}{" "}
              answers
            </small>
          </div>
          <div style={{ marginLeft: "auto" }}>
            <Button
              variant="contained"
              style={{
                backgroundColor: "#09bfad",
                color: "white",
                outline: "none",
                marginLeft: "auto",
              }}
              onClick={toggleModal}
            >
              Answer
            </Button>
          </div>
        </div>
        <hr />
        <Card className={classes.card}>
          <CardHeader
            style={{ backgroundColor: "#f6f3f1" }}
            avatar={
              <Avatar aria-label="recipe" className={classes.avatar}>
                {thread.author[0].toUpperCase()}
              </Avatar>
            }
            title={thread.author}
            subheader={thread.date}
          />
          <CardContent>
            <Typography variant="body2" color="textPrimary" component="p">
              {thread.description}
            </Typography>
          </CardContent>
        </Card>
        <hr />
        <ThreadDetailComponent answers={thread.followups} />
        <hr />
        <div style={{ width: "100%" }}>
          <Button
            variant="contained"
            style={{
              backgroundColor: "#09bfad",
              color: "white",
              outline: "none",
              marginLeft: "auto",
            }}
            onClick={toggleModal}
          >
            Your Answer
          </Button>
        </div>
        <Answer
          open={open}
          handleClose={toggleModal}
          postQuestion={postQuestion}
          setfiles={setfiles}
          files={files}
        />
      </Container>
      <Footer />
    </>
  );
}

function Thread(props) {
  let { thread_id } = useParams();

  const success = () => {
    console.log("success");
  };

  const failed = () => {
    props.history.push("/home");
  };

  useEffect(() => {
    props.fetchThreadDetail(Number(thread_id), success, failed);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  console.log(
    Object.keys(props.forum.threadDetail).length === 0 &&
      props.forum.threadDetail.constructor === Object
  );

  if (
    Object.keys(props.forum.threadDetail).length > 0 &&
    props.forum.threadDetail.constructor === Object
  ) {
    console.log(props.forum.threadDetail);
    return (
      <ThreadDetail
        thread={props.forum.threadDetail}
        followUp={props.followUp}
      />
    );
  } else {
    return (
      <div className="loader">
        <CustomLoader />
      </div>
    );
  }
}

export default Thread;
